.setup-flow {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.setup-flow .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, #1a0b2e 0%, #2d1b4e 100%);
  z-index: 0;
}

.setup-content {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 60px 20px 40px;
}

.setup-icon-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 200px;
  margin-top: 40px;
}

.setup-ripple {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 1px solid rgba(147, 51, 234, 0.3);
  animation: ripple 3s ease-out infinite;
}

.setup-ripple::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 160px;
  height: 160px;
  border-radius: 50%;
  border: 1px solid rgba(147, 51, 234, 0.25);
  animation: ripple 3s ease-out infinite 0.5s;
}

.setup-ripple::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 1px solid rgba(147, 51, 234, 0.2);
  animation: ripple 3s ease-out infinite 1s;
}

.setup-success-ripple {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 1px solid rgba(16, 185, 129, 0.3);
  animation: ripple 3s ease-out infinite;
}

.setup-success-ripple::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 160px;
  height: 160px;
  border-radius: 50%;
  border: 1px solid rgba(16, 185, 129, 0.25);
  animation: ripple 3s ease-out infinite 0.5s;
}

.setup-success-ripple::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 1px solid rgba(16, 185, 129, 0.2);
  animation: ripple 3s ease-out infinite 1s;
}

@keyframes ripple {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(1.5);
    opacity: 0;
  }
}

.setup-icon-wrapper {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}

.setup-icon-image {
  width: 80px;
  height: 80px;
  object-fit: contain;
}

.setup-success-icon {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}

.setup-done-image {
  width: 60px;
  height: 60px;
  object-fit: contain;
}

.button-icon-small {
  width: 20px;
  height: 20px;
  object-fit: contain;
}

.setup-title {
  font-size: 28px;
  font-weight: 600;
  color: white;
  text-align: center;
  margin: 0;
  margin-top: 40px;
}

.setup-description {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  margin: 16px 0 0;
  line-height: 1.5;
  max-width: 320px;
}

.setup-buttons {
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: auto;
  margin-bottom: 20px;
}

.setup-primary-button {
  width: 100%;
  height: 56px;
  border-radius: 16px;
  border: none;
  background: linear-gradient(90deg, #9333ea 0%, #3b82f6 100%);
  color: white;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: transform 0.2s, opacity 0.2s;
}

.setup-primary-button:active {
  transform: scale(0.98);
  opacity: 0.9;
}

.setup-secondary-button {
  width: 100%;
  height: 56px;
  border-radius: 16px;
  border: 1px solid rgba(147, 51, 234, 0.5);
  background: rgba(26, 11, 46, 0.6);
  backdrop-filter: blur(10px);
  color: white;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: transform 0.2s, opacity 0.2s, border-color 0.2s;
}

.setup-secondary-button:active {
  transform: scale(0.98);
  opacity: 0.8;
  border-color: rgba(147, 51, 234, 0.7);
}

.setup-buttons-row {
  display: flex;
